"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { PortableText } from "@portabletext/react";

/* ----------------------------------------
   HELPERS
---------------------------------------- */
function calculateReadTime(content = []) {
  const text = content
    .filter((block) => block._type === "block")
    .flatMap((block) => block.children?.map((c) => c.text) || [])
    .join(" ");

  const words = text.split(/\s+/).length;
  return `${Math.max(1, Math.ceil(words / 200))} min read`;
}

export default function BlogPostClient({ post, relatedPosts }) {
  const [progress, setProgress] = useState(0);
  const [pageUrl, setPageUrl] = useState("");

  useEffect(() => {
    setPageUrl(window.location.href);

    const onScroll = () => {
      const total =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;
      setProgress((window.scrollY / total) * 100);
    };

    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  const readTime = calculateReadTime(post.content);

  return (
    <>
      {/* Progress bar */}
      <div className="fixed top-0 left-0 w-full h-1 bg-gray-200 z-50">
        <div
          className="h-full bg-blue-600"
          style={{ width: `${progress}%` }}
        />
      </div>

      {/* Share sidebar */}
      {pageUrl && (
        <div className="hidden lg:flex fixed right-6 top-1/3 flex-col gap-3 z-40">
          <button
            onClick={() => navigator.clipboard.writeText(pageUrl)}
            className="w-10 h-10 rounded-full border bg-white flex items-center justify-center hover:bg-gray-100"
          >
            üîó
          </button>

          <a
            href={`https://twitter.com/intent/tweet?url=${encodeURIComponent(
              pageUrl
            )}&text=${encodeURIComponent(post.title)}`}
            target="_blank"
          >
            üê¶
          </a>

          <a
            href={`https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(
              pageUrl
            )}`}
            target="_blank"
          >
            üíº
          </a>
        </div>
      )}

      <main className="max-w-4xl mx-auto px-5 py-16 pt-20">
        {/* Categories */}
        <div className="flex gap-2 mb-4">
          {post.categories?.map((cat) => (
            <span
              key={cat._id}
              className="text-xs bg-blue-50 text-blue-600 px-3 py-1 rounded-full"
            >
              {cat.title}
            </span>
          ))}
        </div>

        <h1 className="text-4xl md:text-5xl font-bold mb-4">{post.title}</h1>

        <div className="flex items-center gap-4 text-sm text-gray-500 mb-8">
          {post.author?.image?.asset?.url && (
            <img
              src={post.author.image.asset.url}
              className="w-8 h-8 rounded-full"
            />
          )}
          <span>{post.author?.name}</span>
          <span>
            Published on {new Date(post.publishedAt).toLocaleDateString()}
          </span>
          <span>{readTime}</span>
        </div>

        {post.mainImage?.asset?.url && (
          <img
            src={post.mainImage.asset.url}
            className="w-full max-h-[420px] object-cover rounded-3xl mb-12"
          />
        )}

        <article className="prose prose-lg max-w-none">
          <PortableText value={post.content} />
        </article>

        {relatedPosts.length > 0 && (
          <section className="mt-24">
            <h2 className="text-2xl font-bold mb-8">Related articles</h2>

            <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-8">
              {relatedPosts.map((rp) => (
                <Link
                  key={rp._id}
                  href={`/blog/${rp.slug}`}
                  className="border rounded-2xl overflow-hidden hover:shadow-xl"
                >
                  {rp.mainImage?.asset?.url && (
                    <img
                      src={rp.mainImage.asset.url}
                      className="h-44 w-full object-cover"
                    />
                  )}

                  <div className="p-5">
                    <h3 className="font-semibold">{rp.title}</h3>
                  </div>
                </Link>
              ))}
            </div>
          </section>
        )}
      </main>
    </>
  );
}
